.headerContainer__menu {
	position: relative;
	z-index: 10;
	font-family: $roboto;
	font-weight: normal;
	@include xl-block {
		position: absolute;
		width: 255px;
		right: -255px;
		top: 0;
		background: rgba(0,0,0,0.85);
		height: 100vh;
		min-height: 100%;
		z-index: 10;
		transition: right 0.5s;
		&:before {
			position: absolute;
			content: '';
			width: 50px;
			height: 50px;
			background: url('data:image/svg+xml;base64,');
			background-repeat: no-repeat;
			background-size: cover;
			top: 10px;
			left: -50px;
			cursor: pointer; }
		&_active {
			right: 0;
			transition: right 0.5s; } }
	ul {
		display: flex;
		align-items: center;
		text-align: center;
		list-style: none;
		padding: 0;
		@include xl-block {
			flex-direction: column;
			align-items: center;
			width: 100%;
			overflow: hidden; }
		li {
			position: relative;
			z-index: 10;
			@include xl-block {
				width: 100%; }
			&:hover .menu-down {
				display: block;
				top: 69px;
				left: 0;
				width: 100%;
				@include xl-block {
					position: relative;
					transition: all 0.8s;
					max-height: 100em;
					top: 0;
					transform: translateX(0); } }
			&:hover a {
				background: #33A977;
				color: $white; }
			.menu-down {
				position: absolute;
				display: flex;
				flex-direction: column;
				z-index: 5;
				display: none;
				@include xl-block {
					position: relative;
					display: block;
					overflow: hidden;
					max-height: 0;
					top: 0;
					transition: all 0.5s;
					transform: translateX(320px); }
				a {
					padding: 15px 0;
					color: $white;
					background: $violet;
					margin-top: 4px; } }
			a {
				padding: 25px 20px;
				font-size: $base-font-size;
				&:hover {
					color: $white;
					background: #33A977; } } } }
	a {
		display: block;
		line-height: 19px;
		color: $violet;
		@include xl(color, $white); } }
